@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body::-webkit-scrollbar{
  background-color: transparent;
  width: 5px;
  padding: 10px;
}
body::-webkit-scrollbar-thumb{
  background-color: rgba(255, 255, 255, 0.274);
  border-radius: 50px;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}
body::-webkit-scrollbar-thumb:hover{
background-color: white;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#main{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}   

#dramas{
    font-family: 'Roboto', sans-serif;
    font-weight: 800;
    text-align: left;
    height: 300px;
    width: 200px;
    color: white;
    border-radius: 6px;
    padding: 10px;
    background-color: black;
    margin: 20px;
    transition: all 300ms ease;
}
#dramas img{
    height: 80%;
    width: 100%;
    border-radius: 6px;
    cursor: pointer;
}
#dramas:hover{
    /* margin-top: -10px; */
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
    background-color: white;
    color: black;
}
body{
    color:white;
}
#main{
    position: relative;
}
#poster{
    border-radius: 6px;
    float: left;
    margin: 10px;
}

#main span{
    float: right;
    width: 60%;
}

#cast-info{
    width: 90%;
    display: flex;
    overflow-x: auto;
}

.cast-info-child {
 margin: 10px;
}

#cast-info::-webkit-scrollbar{
    display: none;
}
.cast-info-child  img{
    border-radius: 6px;
    height: 250px;
    width: 200px;
    
}
#episodes{
    width: 100%;
   display: flex;        
   flex-wrap: wrap;
   text-align: left;
   align-items: center;
   justify-content: center;
}
#episodes *{
margin: 5px;
}
#episodes button{
    width: 200px;
    border: 1px solid white;
    color: white;
    padding: 10px;
    outline: none;
    background-color: black;
    border-radius: 3px;
    font-weight: bolder;
    transition: all 400ms ease;
}
#episodes button:hover{
    background-color: white;
    color: black;
}
nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
#search{
    /* background-color: red; */
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    float: right;
    width: 210px;
    border-radius: 50px;
    overflow: hidden;
    border: 1px solid white;
    transition: all 200ms ease;
    margin-left: auto ;
    margin-top: 10px;
    margin-right: 10px;
}
#search input{
    height: 100%;
    margin: 0;
    border: 0;
    background-color: transparent;
    outline: none;
    color: white;
}
#search:hover{
    background-color: white;
    color: black;
}
#search:hover > input{
    color: black;
}

